const _hoisted_2 = /*#__PURE__*/_createStaticVNode("<h1 data-v-7946313e>Datenschutzerklrung</h1><div data-v-7946313e><p data-v-7946313e> Wir freuen uns sehr ber Ihr Interesse an unserem Unternehmen. Datenschutz hat einen besonders hohen Stellenwert fr die Geschftsleitung der Weichwarenprojekt GbR. Eine Nutzung der Internetseiten der Weichwarenprojekt GbR ist grundstzlich ohne jede Angabe personenbezogener Daten mglich. Sofern eine betroffene Person besondere Services unseres Unternehmens ber unsere Internetseite in Anspruch nehmen mchte, knnte jedoch eine Verarbeitung personenbezogener Daten erforderlich werden. Ist die Verarbeitung personenbezogener Daten erforderlich und besteht fr eine solche Verarbeitung keine gesetzliche Grundlage, holen wir generell eine Einwilligung der betroffenen Person ein. </p><p data-v-7946313e> Die Verarbeitung personenbezogener Daten, beispielsweise des Namens, der Anschrift, E-Mail-Adresse oder Telefonnummer einer betroffenen Person, erfolgt stets im Einklang mit der Datenschutz-Grundverordnung und in bereinstimmung mit den fr die Weichwarenprojekt GbR geltenden landesspezifischen Datenschutzbestimmungen. Mittels dieser Datenschutzerklrung mchte unser Unternehmen die ffentlichkeit ber Art, Umfang und Zweck der von uns erhobenen, genutzten und verarbeiteten personenbezogenen Daten informieren. Ferner werden betroffene Personen mittels dieser Datenschutzerklrung ber die ihnen zustehenden Rechte aufgeklrt. </p><p data-v-7946313e> Die Weichwarenprojekt GbR hat als fr die Verarbeitung Verantwortlicher zahlreiche technische und organisatorische Manahmen umgesetzt, um einen mglichst lckenlosen Schutz der ber diese Internetseite verarbeiteten personenbezogenen Daten sicherzustellen. Dennoch knnen Internetbasierte Datenbertragungen grundstzlich Sicherheitslcken aufweisen, sodass ein absoluter Schutz nicht gewhrleistet werden kann. Aus diesem Grund steht es jeder betroffenen Person frei, personenbezogene Daten auch auf alternativen Wegen, beispielsweise telefonisch, an uns zu bermitteln. </p><h4 data-v-7946313e>1. Begriffsbestimmungen</h4><p data-v-7946313e> Die Datenschutzerklrung der Weichwarenprojekt GbR beruht auf den Begrifflichkeiten, die durch den Europischen Richtlinien- und Verordnungsgeber beim Erlass der Datenschutz-Grundverordnung (DS-GVO) verwendet wurden. Unsere Datenschutzerklrung soll sowohl fr die ffentlichkeit als auch fr unsere Kunden und Geschftspartner einfach lesbar und verstndlich sein. Um dies zu gewhrleisten, mchten wir vorab die verwendeten Begrifflichkeiten erlutern. </p><p data-v-7946313e>Wir verwenden in dieser Datenschutzerklrung unter anderem die folgenden Begriffe:</p><ul style=\"list-style:none;\" data-v-7946313e><li data-v-7946313e><h4 data-v-7946313e>a) personenbezogene Daten</h4><p data-v-7946313e> Personenbezogene Daten sind alle Informationen, die sich auf eine identifizierte oder identifizierbare natrliche Person (im Folgenden betroffene Person) beziehen. Als identifizierbar wird eine natrliche Person angesehen, die direkt oder indirekt, insbesondere mittels Zuordnung zu einer Kennung wie einem Namen, zu einer Kennnummer, zu Standortdaten, zu einer Online-Kennung oder zu einem oder mehreren besonderen Merkmalen, die Ausdruck der physischen, physiologischen, genetischen, psychischen, wirtschaftlichen, kulturellen oder sozialen Identitt dieser natrlichen Person sind, identifiziert werden kann. </p></li><li data-v-7946313e><h4 data-v-7946313e>b) betroffene Person</h4><p data-v-7946313e> Betroffene Person ist jede identifizierte oder identifizierbare natrliche Person, deren personenbezogene Daten von dem fr die Verarbeitung Verantwortlichen verarbeitet werden. </p></li><li data-v-7946313e><h4 data-v-7946313e>c) Verarbeitung</h4><p data-v-7946313e> Verarbeitung ist jeder mit oder ohne Hilfe automatisierter Verfahren ausgefhrte Vorgang oder jede solche Vorgangsreihe im Zusammenhang mit personenbezogenen Daten wie das Erheben, das Erfassen, die Organisation, das Ordnen, die Speicherung, die Anpassung oder Vernderung, das Auslesen, das Abfragen, die Verwendung, die Offenlegung durch bermittlung, Verbreitung oder eine andere Form der Bereitstellung, den Abgleich oder die Verknpfung, die Einschrnkung, das Lschen oder die Vernichtung. </p></li><li data-v-7946313e><h4 data-v-7946313e>d) Einschrnkung der Verarbeitung</h4><p data-v-7946313e> Einschrnkung der Verarbeitung ist die Markierung gespeicherter personenbezogener Daten mit dem Ziel, ihre knftige Verarbeitung einzuschrnken. </p></li><li data-v-7946313e><h4 data-v-7946313e>e) Profiling</h4><p data-v-7946313e> Profiling ist jede Art der automatisierten Verarbeitung personenbezogener Daten, die darin besteht, dass diese personenbezogenen Daten verwendet werden, um bestimmte persnliche Aspekte, die sich auf eine natrliche Person beziehen, zu bewerten, insbesondere, um Aspekte bezglich Arbeitsleistung, wirtschaftlicher Lage, Gesundheit, persnlicher Vorlieben, Interessen, Zuverlssigkeit, Verhalten, Aufenthaltsort oder Ortswechsel dieser natrlichen Person zu analysieren oder vorherzusagen. </p></li><li data-v-7946313e><h4 data-v-7946313e>f) Pseudonymisierung</h4><p data-v-7946313e> Pseudonymisierung ist die Verarbeitung personenbezogener Daten in einer Weise, auf welche die personenbezogenen Daten ohne Hinzuziehung zustzlicher Informationen nicht mehr einer spezifischen betroffenen Person zugeordnet werden knnen, sofern diese zustzlichen Informationen gesondert aufbewahrt werden und technischen und organisatorischen Manahmen unterliegen, die gewhrleisten, dass die personenbezogenen Daten nicht einer identifizierten oder identifizierbaren natrlichen Person zugewiesen werden. </p></li><li data-v-7946313e><h4 data-v-7946313e>g) Verantwortlicher oder fr die Verarbeitung Verantwortlicher</h4><p data-v-7946313e> Verantwortlicher oder fr die Verarbeitung Verantwortlicher ist die natrliche oder juristische Person, Behrde, Einrichtung oder andere Stelle, die allein oder gemeinsam mit anderen ber die Zwecke und Mittel der Verarbeitung von personenbezogenen Daten entscheidet. Sind die Zwecke und Mittel dieser Verarbeitung durch das Unionsrecht oder das Recht der Mitgliedstaaten vorgegeben, so kann der Verantwortliche beziehungsweise knnen die bestimmten Kriterien seiner Benennung nach dem Unionsrecht oder dem Recht der Mitgliedstaaten vorgesehen werden. </p></li><li data-v-7946313e><h4 data-v-7946313e>h) Auftragsverarbeiter</h4><p data-v-7946313e> Auftragsverarbeiter ist eine natrliche oder juristische Person, Behrde, Einrichtung oder andere Stelle, die personenbezogene Daten im Auftrag des Verantwortlichen verarbeitet. </p></li><li data-v-7946313e><h4 data-v-7946313e>i) Empfnger</h4><p data-v-7946313e> Empfnger ist eine natrliche oder juristische Person, Behrde, Einrichtung oder andere Stelle, der personenbezogene Daten offengelegt werden, unabhngig davon, ob es sich bei ihr um einen Dritten handelt oder nicht. Behrden, die im Rahmen eines bestimmten Untersuchungsauftrags nach dem Unionsrecht oder dem Recht der Mitgliedstaaten mglicherweise personenbezogene Daten erhalten, gelten jedoch nicht als Empfnger. </p></li><li data-v-7946313e><h4 data-v-7946313e>j) Dritter</h4><p data-v-7946313e> Dritter ist eine natrliche oder juristische Person, Behrde, Einrichtung oder andere Stelle auer der betroffenen Person, dem Verantwortlichen, dem Auftragsverarbeiter und den Personen, die unter der unmittelbaren Verantwortung des Verantwortlichen oder des Auftragsverarbeiters befugt sind, die personenbezogenen Daten zu verarbeiten. </p></li><li data-v-7946313e><h4 data-v-7946313e>k) Einwilligung</h4><p data-v-7946313e> Einwilligung ist jede von der betroffenen Person freiwillig fr den bestimmten Fall in informierter Weise und unmissverstndlich abgegebene Willensbekundung in Form einer Erklrung oder einer sonstigen eindeutigen besttigenden Handlung, mit der die betroffene Person zu verstehen gibt, dass sie mit der Verarbeitung der sie betreffenden personenbezogenen Daten einverstanden ist. </p></li></ul><h4 data-v-7946313e>2. Name und Anschrift des fr die Verarbeitung Verantwortlichen</h4><p data-v-7946313e> Verantwortlicher im Sinne der Datenschutz-Grundverordnung, sonstiger in den Mitgliedstaaten der Europischen Union geltenden Datenschutzgesetze und anderer Bestimmungen mit datenschutzrechtlichem Charakter ist die: </p><p data-v-7946313e>Weichwarenprojekt GbR</p><p data-v-7946313e>Hauptstrae 48</p><p data-v-7946313e>89614 pfingen</p><p data-v-7946313e>Deutschland</p><p data-v-7946313e>Tel.: -</p><p data-v-7946313e>E-Mail: info@weichwarenprojekt.de</p><p data-v-7946313e>Website: www.weichwarenprojekt.de</p><h4 data-v-7946313e>3. Erfassung von allgemeinen Daten und Informationen</h4><p data-v-7946313e> Die Internetseite der Weichwarenprojekt GbR erfasst mit jedem Aufruf der Internetseite durch eine betroffene Person oder ein automatisiertes System eine Reihe von allgemeinen Daten und Informationen. Diese allgemeinen Daten und Informationen werden in den Logfiles des Servers gespeichert. Erfasst werden knnen die (1) verwendeten Browsertypen und Versionen, (2) das vom zugreifenden System verwendete Betriebssystem, (3) die Internetseite, von welcher ein zugreifendes System auf unsere Internetseite gelangt (sogenannte Referrer), (4) die Unterwebseiten, welche ber ein zugreifendes System auf unserer Internetseite angesteuert werden, (5) das Datum und die Uhrzeit eines Zugriffs auf die Internetseite, (6) eine Internet-Protokoll-Adresse (IP-Adresse), (7) der Internet-Service-Provider des zugreifenden Systems und (8) sonstige hnliche Daten und Informationen, die der Gefahrenabwehr im Falle von Angriffen auf unsere informationstechnologischen Systeme dienen. </p><p data-v-7946313e> Bei der Nutzung dieser allgemeinen Daten und Informationen zieht die Weichwarenprojekt GbR keine Rckschlsse auf die betroffene Person. Diese Informationen werden vielmehr bentigt, um (1) die Inhalte unserer Internetseite korrekt auszuliefern, (2) die Inhalte unserer Internetseite sowie die Werbung fr diese zu optimieren, (3) die dauerhafte Funktionsfhigkeit unserer informationstechnologischen Systeme und der Technik unserer Internetseite zu gewhrleisten sowie (4) um Strafverfolgungsbehrden im Falle eines Cyberangriffes die zur Strafverfolgung notwendigen Informationen bereitzustellen. Diese anonym erhobenen Daten und Informationen werden durch die Weichwarenprojekt GbR daher einerseits statistisch und ferner mit dem Ziel ausgewertet, den Datenschutz und die Datensicherheit in unserem Unternehmen zu erhhen, um letztlich ein optimales Schutzniveau fr die von uns verarbeiteten personenbezogenen Daten sicherzustellen. Die anonymen Daten der Server-Logfiles werden getrennt von allen durch eine betroffene Person angegebenen personenbezogenen Daten gespeichert. </p><h4 data-v-7946313e>4. Kontaktmglichkeit ber die Internetseite</h4><p data-v-7946313e> Die Internetseite der Weichwarenprojekt GbR enthlt aufgrund von gesetzlichen Vorschriften Angaben, die eine schnelle elektronische Kontaktaufnahme zu unserem Unternehmen sowie eine unmittelbare Kommunikation mit uns ermglichen, was ebenfalls eine allgemeine Adresse der sogenannten elektronischen Post (E-Mail-Adresse) umfasst. Sofern eine betroffene Person per E-Mail oder ber ein Kontaktformular den Kontakt mit dem fr die Verarbeitung Verantwortlichen aufnimmt, werden die von der betroffenen Person bermittelten personenbezogenen Daten automatisch gespeichert. Solche auf freiwilliger Basis von einer betroffenen Person an den fr die Verarbeitung Verantwortlichen bermittelten personenbezogenen Daten werden fr Zwecke der Bearbeitung oder der Kontaktaufnahme zur betroffenen Person gespeichert. Es erfolgt keine Weitergabe dieser personenbezogenen Daten an Dritte. </p><h4 data-v-7946313e>5. Routinemige Lschung und Sperrung von personenbezogenen Daten</h4><p data-v-7946313e> Der fr die Verarbeitung Verantwortliche verarbeitet und speichert personenbezogene Daten der betroffenen Person nur fr den Zeitraum, der zur Erreichung des Speicherungszwecks erforderlich ist oder sofern dies durch den Europischen Richtlinien- und Verordnungsgeber oder einen anderen Gesetzgeber in Gesetzen oder Vorschriften, welchen der fr die Verarbeitung Verantwortliche unterliegt, vorgesehen wurde. </p><p data-v-7946313e> Entfllt der Speicherungszweck oder luft eine vom Europischen Richtlinien- und Verordnungsgeber oder einem anderen zustndigen Gesetzgeber vorgeschriebene Speicherfrist ab, werden die personenbezogenen Daten routinemig und entsprechend den gesetzlichen Vorschriften gesperrt oder gelscht. </p><h4 data-v-7946313e>6. Rechte der betroffenen Person</h4><ul style=\"list-style:none;\" data-v-7946313e><li data-v-7946313e><h4 data-v-7946313e>a) Recht auf Besttigung</h4><p data-v-7946313e> Jede betroffene Person hat das vom Europischen Richtlinien- und Verordnungsgeber eingerumte Recht, von dem fr die Verarbeitung Verantwortlichen eine Besttigung darber zu verlangen, ob sie betreffende personenbezogene Daten verarbeitet werden. Mchte eine betroffene Person dieses Besttigungsrecht in Anspruch nehmen, kann sie sich hierzu jederzeit an einen Mitarbeiter des fr die Verarbeitung Verantwortlichen wenden. </p></li><li data-v-7946313e><h4 data-v-7946313e>b) Recht auf Auskunft</h4><p data-v-7946313e> Jede von der Verarbeitung personenbezogener Daten betroffene Person hat das vom Europischen Richtlinien- und Verordnungsgeber gewhrte Recht, jederzeit von dem fr die Verarbeitung Verantwortlichen unentgeltliche Auskunft ber die zu seiner Person gespeicherten personenbezogenen Daten und eine Kopie dieser Auskunft zu erhalten. Ferner hat der Europische Richtlinien- und Verordnungsgeber der betroffenen Person Auskunft ber folgende Informationen zugestanden: </p><ul style=\"list-style:none;\" data-v-7946313e><li data-v-7946313e>die Verarbeitungszwecke</li><li data-v-7946313e>die Kategorien personenbezogener Daten, die verarbeitet werden</li><li data-v-7946313e> die Empfnger oder Kategorien von Empfngern, gegenber denen die personenbezogenen Daten offengelegt worden sind oder noch offengelegt werden, insbesondere bei Empfngern in Drittlndern oder bei internationalen Organisationen </li><li data-v-7946313e> falls mglich die geplante Dauer, fr die die personenbezogenen Daten gespeichert werden, oder, falls dies nicht mglich ist, die Kriterien fr die Festlegung dieser Dauer </li><li data-v-7946313e> das Bestehen eines Rechts auf Berichtigung oder Lschung der sie betreffenden personenbezogenen Daten oder auf Einschrnkung der Verarbeitung durch den Verantwortlichen oder eines Widerspruchsrechts gegen diese Verarbeitung </li><li data-v-7946313e>das Bestehen eines Beschwerderechts bei einer Aufsichtsbehrde</li><li data-v-7946313e> wenn die personenbezogenen Daten nicht bei der betroffenen Person erhoben werden: Alle verfgbaren Informationen ber die Herkunft der Daten </li><li data-v-7946313e> das Bestehen einer automatisierten Entscheidungsfindung einschlielich Profiling gem Artikel 22 Abs.1 und 4 DS-GVO und  zumindest in diesen Fllen  aussagekrftige Informationen ber die involvierte Logik sowie die Tragweite und die angestrebten Auswirkungen einer derartigen Verarbeitung fr die betroffene Person </li></ul><p data-v-7946313e> Ferner steht der betroffenen Person ein Auskunftsrecht darber zu, ob personenbezogene Daten an ein Drittland oder an eine internationale Organisation bermittelt wurden. Sofern dies der Fall ist, so steht der betroffenen Person im brigen das Recht zu, Auskunft ber die geeigneten Garantien im Zusammenhang mit der bermittlung zu erhalten. </p><p data-v-7946313e> Mchte eine betroffene Person dieses Auskunftsrecht in Anspruch nehmen, kann sie sich hierzu jederzeit an einen Mitarbeiter des fr die Verarbeitung Verantwortlichen wenden. </p></li><li data-v-7946313e><h4 data-v-7946313e>c) Recht auf Berichtigung</h4><p data-v-7946313e> Jede von der Verarbeitung personenbezogener Daten betroffene Person hat das vom Europischen Richtlinien- und Verordnungsgeber gewhrte Recht, die unverzgliche Berichtigung sie betreffender unrichtiger personenbezogener Daten zu verlangen. Ferner steht der betroffenen Person das Recht zu, unter Bercksichtigung der Zwecke der Verarbeitung, die Vervollstndigung unvollstndiger personenbezogener Daten  auch mittels einer ergnzenden Erklrung  zu verlangen. </p><p data-v-7946313e> Mchte eine betroffene Person dieses Berichtigungsrecht in Anspruch nehmen, kann sie sich hierzu jederzeit an einen Mitarbeiter des fr die Verarbeitung Verantwortlichen wenden. </p></li><li data-v-7946313e><h4 data-v-7946313e>d) Recht auf Lschung (Recht auf Vergessen werden)</h4><p data-v-7946313e> Jede von der Verarbeitung personenbezogener Daten betroffene Person hat das vom Europischen Richtlinien- und Verordnungsgeber gewhrte Recht, von dem Verantwortlichen zu verlangen, dass die sie betreffenden personenbezogenen Daten unverzglich gelscht werden, sofern einer der folgenden Grnde zutrifft und soweit die Verarbeitung nicht erforderlich ist: </p><ul style=\"list-style:none;\" data-v-7946313e><li data-v-7946313e> Die personenbezogenen Daten wurden fr solche Zwecke erhoben oder auf sonstige Weise verarbeitet, fr welche sie nicht mehr notwendig sind. </li><li data-v-7946313e> Die betroffene Person widerruft ihre Einwilligung, auf die sich die Verarbeitung gem Art. 6 Abs. 1 Buchstabe a DS-GVO oder Art. 9 Abs. 2 Buchstabe a DS-GVO sttzte, und es fehlt an einer anderweitigen Rechtsgrundlage fr die Verarbeitung. </li><li data-v-7946313e> Die betroffene Person legt gem Art. 21 Abs. 1 DS-GVO Widerspruch gegen die Verarbeitung ein, und es liegen keine vorrangigen berechtigten Grnde fr die Verarbeitung vor, oder die betroffene Person legt gem Art. 21 Abs. 2 DS-GVO Widerspruch gegen die Verarbeitung ein. </li><li data-v-7946313e>Die personenbezogenen Daten wurden unrechtmig verarbeitet.</li><li data-v-7946313e> Die Lschung der personenbezogenen Daten ist zur Erfllung einer rechtlichen Verpflichtung nach dem Unionsrecht oder dem Recht der Mitgliedstaaten erforderlich, dem der Verantwortliche unterliegt. </li><li data-v-7946313e> Die personenbezogenen Daten wurden in Bezug auf angebotene Dienste der Informationsgesellschaft gem Art. 8 Abs. 1 DS-GVO erhoben. </li></ul><p data-v-7946313e> Sofern einer der oben genannten Grnde zutrifft und eine betroffene Person die Lschung von personenbezogenen Daten, die bei der Weichwarenprojekt GbR gespeichert sind, veranlassen mchte, kann sie sich hierzu jederzeit an einen Mitarbeiter des fr die Verarbeitung Verantwortlichen wenden. Der Mitarbeiter der Weichwarenprojekt GbR wird veranlassen, dass dem Lschverlangen unverzglich nachgekommen wird. </p><p data-v-7946313e> Wurden die personenbezogenen Daten von der Weichwarenprojekt GbR ffentlich gemacht und ist unser Unternehmen als Verantwortlicher gem Art. 17 Abs. 1 DS-GVO zur Lschung der personenbezogenen Daten verpflichtet, so trifft die Weichwarenprojekt GbR unter Bercksichtigung der verfgbaren Technologie und der Implementierungskosten angemessene Manahmen, auch technischer Art, um andere fr die Datenverarbeitung Verantwortliche, welche die verffentlichten personenbezogenen Daten verarbeiten, darber in Kenntnis zu setzen, dass die betroffene Person von diesen anderen fr die Datenverarbeitung Verantwortlichen die Lschung smtlicher Links zu diesen personenbezogenen Daten oder von Kopien oder Replikationen dieser personenbezogenen Daten verlangt hat, soweit die Verarbeitung nicht erforderlich ist. Der Mitarbeiter der Weichwarenprojekt GbR wird im Einzelfall das Notwendige veranlassen. </p></li><li data-v-7946313e><h4 data-v-7946313e>e) Recht auf Einschrnkung der Verarbeitung</h4><p data-v-7946313e> Jede von der Verarbeitung personenbezogener Daten betroffene Person hat das vom Europischen Richtlinien- und Verordnungsgeber gewhrte Recht, von dem Verantwortlichen die Einschrnkung der Verarbeitung zu verlangen, wenn eine der folgenden Voraussetzungen gegeben ist: </p><ul style=\"list-style:none;\" data-v-7946313e><li data-v-7946313e> Die Richtigkeit der personenbezogenen Daten wird von der betroffenen Person bestritten, und zwar fr eine Dauer, die es dem Verantwortlichen ermglicht, die Richtigkeit der personenbezogenen Daten zu berprfen. </li><li data-v-7946313e> Die Verarbeitung ist unrechtmig, die betroffene Person lehnt die Lschung der personenbezogenen Daten ab und verlangt stattdessen die Einschrnkung der Nutzung der personenbezogenen Daten. </li><li data-v-7946313e> Der Verantwortliche bentigt die personenbezogenen Daten fr die Zwecke der Verarbeitung nicht lnger, die betroffene Person bentigt sie jedoch zur Geltendmachung, Ausbung oder Verteidigung von Rechtsansprchen. </li><li data-v-7946313e> Die betroffene Person hat Widerspruch gegen die Verarbeitung gem. Art. 21 Abs. 1 DS-GVO eingelegt und es steht noch nicht fest, ob die berechtigten Grnde des Verantwortlichen gegenber denen der betroffenen Person berwiegen. </li></ul><p data-v-7946313e> Sofern eine der oben genannten Voraussetzungen gegeben ist und eine betroffene Person die Einschrnkung von personenbezogenen Daten, die bei der Weichwarenprojekt GbR gespeichert sind, verlangen mchte, kann sie sich hierzu jederzeit an einen Mitarbeiter des fr die Verarbeitung Verantwortlichen wenden. Der Mitarbeiter der Weichwarenprojekt GbR wird die Einschrnkung der Verarbeitung veranlassen. </p></li><li data-v-7946313e><h4 data-v-7946313e>f) Recht auf Datenbertragbarkeit</h4><p data-v-7946313e> Jede von der Verarbeitung personenbezogener Daten betroffene Person hat das vom Europischen Richtlinien- und Verordnungsgeber gewhrte Recht, die sie betreffenden personenbezogenen Daten, welche durch die betroffene Person einem Verantwortlichen bereitgestellt wurden, in einem strukturierten, gngigen und maschinenlesbaren Format zu erhalten. Sie hat auerdem das Recht, diese Daten einem anderen Verantwortlichen ohne Behinderung durch den Verantwortlichen, dem die personenbezogenen Daten bereitgestellt wurden, zu bermitteln, sofern die Verarbeitung auf der Einwilligung gem Art. 6 Abs. 1 Buchstabe a DS-GVO oder Art. 9 Abs. 2 Buchstabe a DS-GVO oder auf einem Vertrag gem Art. 6 Abs. 1 Buchstabe b DS-GVO beruht und die Verarbeitung mithilfe automatisierter Verfahren erfolgt, sofern die Verarbeitung nicht fr die Wahrnehmung einer Aufgabe erforderlich ist, die im ffentlichen Interesse liegt oder in Ausbung ffentlicher Gewalt erfolgt, welche dem Verantwortlichen bertragen wurde. </p><p data-v-7946313e> Ferner hat die betroffene Person bei der Ausbung ihres Rechts auf Datenbertragbarkeit gem Art. 20 Abs. 1 DS-GVO das Recht, zu erwirken, dass die personenbezogenen Daten direkt von einem Verantwortlichen an einen anderen Verantwortlichen bermittelt werden, soweit dies technisch machbar ist und sofern hiervon nicht die Rechte und Freiheiten anderer Personen beeintrchtigt werden. </p><p data-v-7946313e> Zur Geltendmachung des Rechts auf Datenbertragbarkeit kann sich die betroffene Person jederzeit an einen Mitarbeiter der Weichwarenprojekt GbR wenden. </p></li><li data-v-7946313e><h4 data-v-7946313e>g) Recht auf Widerspruch</h4><p data-v-7946313e> Jede von der Verarbeitung personenbezogener Daten betroffene Person hat das vom Europischen Richtlinien- und Verordnungsgeber gewhrte Recht, aus Grnden, die sich aus ihrer besonderen Situation ergeben, jederzeit gegen die Verarbeitung sie betreffender personenbezogener Daten, die aufgrund von Art. 6 Abs. 1 Buchstaben e oder f DS-GVO erfolgt, Widerspruch einzulegen. Dies gilt auch fr ein auf diese Bestimmungen gesttztes Profiling. </p><p data-v-7946313e> Die Weichwarenprojekt GbR verarbeitet die personenbezogenen Daten im Falle des Widerspruchs nicht mehr, es sei denn, wir knnen zwingende schutzwrdige Grnde fr die Verarbeitung nachweisen, die den Interessen, Rechten und Freiheiten der betroffenen Person berwiegen, oder die Verarbeitung dient der Geltendmachung, Ausbung oder Verteidigung von Rechtsansprchen. </p><p data-v-7946313e> Verarbeitet die Weichwarenprojekt GbR personenbezogene Daten, um Direktwerbung zu betreiben, so hat die betroffene Person das Recht, jederzeit Widerspruch gegen die Verarbeitung der personenbezogenen Daten zum Zwecke derartiger Werbung einzulegen. Dies gilt auch fr das Profiling, soweit es mit solcher Direktwerbung in Verbindung steht. Widerspricht die betroffene Person gegenber der Weichwarenprojekt GbR der Verarbeitung fr Zwecke der Direktwerbung, so wird die Weichwarenprojekt GbR die personenbezogenen Daten nicht mehr fr diese Zwecke verarbeiten. </p><p data-v-7946313e> Zudem hat die betroffene Person das Recht, aus Grnden, die sich aus ihrer besonderen Situation ergeben, gegen die sie betreffende Verarbeitung personenbezogener Daten, die bei der Weichwarenprojekt GbR zu wissenschaftlichen oder historischen Forschungszwecken oder zu statistischen Zwecken gem Art. 89 Abs. 1 DS-GVO erfolgen, Widerspruch einzulegen, es sei denn, eine solche Verarbeitung ist zur Erfllung einer im ffentlichen Interesse liegenden Aufgabe erforderlich. </p><p data-v-7946313e> Zur Ausbung des Rechts auf Widerspruch kann sich die betroffene Person direkt an jeden Mitarbeiter der Weichwarenprojekt GbR oder einen anderen Mitarbeiter wenden. Der betroffenen Person steht es ferner frei, im Zusammenhang mit der Nutzung von Diensten der Informationsgesellschaft, ungeachtet der Richtlinie 2002/58/EG, ihr Widerspruchsrecht mittels automatisierter Verfahren auszuben, bei denen technische Spezifikationen verwendet werden. </p></li><li data-v-7946313e><h4 data-v-7946313e>h) Automatisierte Entscheidungen im Einzelfall einschlielich Profiling</h4><p data-v-7946313e> Jede von der Verarbeitung personenbezogener Daten betroffene Person hat das vom Europischen Richtlinien- und Verordnungsgeber gewhrte Recht, nicht einer ausschlielich auf einer automatisierten Verarbeitung  einschlielich Profiling  beruhenden Entscheidung unterworfen zu werden, die ihr gegenber rechtliche Wirkung entfaltet oder sie in hnlicher Weise erheblich beeintrchtigt, sofern die Entscheidung (1) nicht fr den Abschluss oder die Erfllung eines Vertrags zwischen der betroffenen Person und dem Verantwortlichen erforderlich ist, oder (2) aufgrund von Rechtsvorschriften der Union oder der Mitgliedstaaten, denen der Verantwortliche unterliegt, zulssig ist und diese Rechtsvorschriften angemessene Manahmen zur Wahrung der Rechte und Freiheiten sowie der berechtigten Interessen der betroffenen Person enthalten oder (3) mit ausdrcklicher Einwilligung der betroffenen Person erfolgt. </p><p data-v-7946313e> Ist die Entscheidung (1) fr den Abschluss oder die Erfllung eines Vertrags zwischen der betroffenen Person und dem Verantwortlichen erforderlich oder (2) erfolgt sie mit ausdrcklicher Einwilligung der betroffenen Person, trifft die Weichwarenprojekt GbR angemessene Manahmen, um die Rechte und Freiheiten sowie die berechtigten Interessen der betroffenen Person zu wahren, wozu mindestens das Recht auf Erwirkung des Eingreifens einer Person seitens des Verantwortlichen, auf Darlegung des eigenen Standpunkts und auf Anfechtung der Entscheidung gehrt. </p><p data-v-7946313e> Mchte die betroffene Person Rechte mit Bezug auf automatisierte Entscheidungen geltend machen, kann sie sich hierzu jederzeit an einen Mitarbeiter des fr die Verarbeitung Verantwortlichen wenden. </p></li><li data-v-7946313e><h4 data-v-7946313e>i) Recht auf Widerruf einer datenschutzrechtlichen Einwilligung</h4><p data-v-7946313e> Jede von der Verarbeitung personenbezogener Daten betroffene Person hat das vom Europischen Richtlinien- und Verordnungsgeber gewhrte Recht, eine Einwilligung zur Verarbeitung personenbezogener Daten jederzeit zu widerrufen. </p><p data-v-7946313e> Mchte die betroffene Person ihr Recht auf Widerruf einer Einwilligung geltend machen, kann sie sich hierzu jederzeit an einen Mitarbeiter des fr die Verarbeitung Verantwortlichen wenden. </p></li></ul><h4 data-v-7946313e>7. Rechtsgrundlage der Verarbeitung</h4><p data-v-7946313e> Art. 6 I lit. a DS-GVO dient unserem Unternehmen als Rechtsgrundlage fr Verarbeitungsvorgnge, bei denen wir eine Einwilligung fr einen bestimmten Verarbeitungszweck einholen. Ist die Verarbeitung personenbezogener Daten zur Erfllung eines Vertrags, dessen Vertragspartei die betroffene Person ist, erforderlich, wie dies beispielsweise bei Verarbeitungsvorgngen der Fall ist, die fr eine Lieferung von Waren oder die Erbringung einer sonstigen Leistung oder Gegenleistung notwendig sind, so beruht die Verarbeitung auf Art. 6 I lit. b DS-GVO. Gleiches gilt fr solche Verarbeitungsvorgnge die zur Durchfhrung vorvertraglicher Manahmen erforderlich sind, etwa in Fllen von Anfragen zur unseren Produkten oder Leistungen. Unterliegt unser Unternehmen einer rechtlichen Verpflichtung durch welche eine Verarbeitung von personenbezogenen Daten erforderlich wird, wie beispielsweise zur Erfllung steuerlicher Pflichten, so basiert die Verarbeitung auf Art. 6 I lit. c DS-GVO. In seltenen Fllen knnte die Verarbeitung von personenbezogenen Daten erforderlich werden, um lebenswichtige Interessen der betroffenen Person oder einer anderen natrlichen Person zu schtzen. Dies wre beispielsweise der Fall, wenn ein Besucher in unserem Betrieb verletzt werden wrde und daraufhin sein Name, sein Alter, seine Krankenkassendaten oder sonstige lebenswichtige Informationen an einen Arzt, ein Krankenhaus oder sonstige Dritte weitergegeben werden mssten. Dann wrde die Verarbeitung auf Art. 6 I lit. d DS-GVO beruhen. Letztlich knnten Verarbeitungsvorgnge auf Art. 6 I lit. f DS-GVO beruhen. Auf dieser Rechtsgrundlage basieren Verarbeitungsvorgnge, die von keiner der vorgenannten Rechtsgrundlagen erfasst werden, wenn die Verarbeitung zur Wahrung eines berechtigten Interesses unseres Unternehmens oder eines Dritten erforderlich ist, sofern die Interessen, Grundrechte und Grundfreiheiten des Betroffenen nicht berwiegen. Solche Verarbeitungsvorgnge sind uns insbesondere deshalb gestattet, weil sie durch den Europischen Gesetzgeber besonders erwhnt wurden. Er vertrat insoweit die Auffassung, dass ein berechtigtes Interesse anzunehmen sein knnte, wenn die betroffene Person ein Kunde des Verantwortlichen ist (Erwgungsgrund 47 Satz 2 DS-GVO). </p><h4 data-v-7946313e> 8. Berechtigte Interessen an der Verarbeitung, die von dem Verantwortlichen oder einem Dritten verfolgt werden </h4><p data-v-7946313e> Basiert die Verarbeitung personenbezogener Daten auf Artikel 6 I lit. f DS-GVO ist unser berechtigtes Interesse die Durchfhrung unserer Geschftsttigkeit zugunsten des Wohlergehens all unserer Mitarbeiter und unserer Anteilseigner. </p><h4 data-v-7946313e>9. Dauer, fr die die personenbezogenen Daten gespeichert werden</h4><p data-v-7946313e> Das Kriterium fr die Dauer der Speicherung von personenbezogenen Daten ist die jeweilige gesetzliche Aufbewahrungsfrist. Nach Ablauf der Frist werden die entsprechenden Daten routinemig gelscht, sofern sie nicht mehr zur Vertragserfllung oder Vertragsanbahnung erforderlich sind. </p><h4 data-v-7946313e> 10. Gesetzliche oder vertragliche Vorschriften zur Bereitstellung der personenbezogenen Daten; Erforderlichkeit fr den Vertragsabschluss; Verpflichtung der betroffenen Person, die personenbezogenen Daten bereitzustellen; mgliche Folgen der Nichtbereitstellung </h4><p data-v-7946313e> Wir klren Sie darber auf, dass die Bereitstellung personenbezogener Daten zum Teil gesetzlich vorgeschrieben ist (z.B. Steuervorschriften) oder sich auch aus vertraglichen Regelungen (z.B. Angaben zum Vertragspartner) ergeben kann. Mitunter kann es zu einem Vertragsschluss erforderlich sein, dass eine betroffene Person uns personenbezogene Daten zur Verfgung stellt, die in der Folge durch uns verarbeitet werden mssen. Die betroffene Person ist beispielsweise verpflichtet uns personenbezogene Daten bereitzustellen, wenn unser Unternehmen mit ihr einen Vertrag abschliet. Eine Nichtbereitstellung der personenbezogenen Daten htte zur Folge, dass der Vertrag mit dem Betroffenen nicht geschlossen werden knnte. Vor einer Bereitstellung personenbezogener Daten durch den Betroffenen muss sich der Betroffene an einen unserer Mitarbeiter wenden. Unser Mitarbeiter klrt den Betroffenen einzelfallbezogen darber auf, ob die Bereitstellung der personenbezogenen Daten gesetzlich oder vertraglich vorgeschrieben oder fr den Vertragsabschluss erforderlich ist, ob eine Verpflichtung besteht, die personenbezogenen Daten bereitzustellen, und welche Folgen die Nichtbereitstellung der personenbezogenen Daten htte. </p><h4 data-v-7946313e>11. Bestehen einer automatisierten Entscheidungsfindung</h4><p data-v-7946313e> Als verantwortungsbewusstes Unternehmen verzichten wir auf eine automatische Entscheidungsfindung oder ein Profiling. </p><p data-v-7946313e> Diese Datenschutzerklrung wurde durch den <a href=\"https://dg-datenschutz.de/datenschutzerklaerung/\" rel=\"nofollow\" data-v-7946313e>Datenschutzerklrungs-Generator</a> der Deutschen Gesellschaft fr Datenschutz, in Kooperation mit den <a href=\"https://www.wbs-law.de/taetigkeitsgebiete/medienrecht/\" data-v-7946313e>Medienrecht</a> Anwlten WILDE BEUGER SOLMECKE | Rechtsanwlte erstellt. </p></div>", 2)
